import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { LightOrder } from '../../../../core/models/order';
import { getUserOrders } from '../../../reducers/selector';
import { Store } from '@ngrx/store';
import { AppState } from '../../../../interfaces';
import { Subscription } from 'rxjs/Subscription';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-order-ajust',
  templateUrl: './order-ajust.component.html',
  styleUrls: ['./order-ajust.component.scss']
})
export class OrderAjustComponent implements OnInit {
  orders$: Observable<LightOrder[]>;
  routeSubscription$: Subscription;
  origin_item: string;

  constructor(
    private store: Store<AppState>,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.orders$ = this.store.select(getUserOrders);

  }

  ngOnInit() {
    this.routeSubscription$ = this.route.params.subscribe(
      (params: any) => {
        this.origin_item = params['number'];
      });    
  }
}



